import React from 'react'
import { Link } from 'react-router-dom'

class Reset extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            email: '',
        }
    }

    handleClick = () => {
        this.setState({
            email: this.refs.email.value,
        })
        this.props.history.push('/')
    }

    render() {
        return (
            <div className="peers ai-s fxw-nw h-100vh">
                <div className="d-n@sm- peer peer-greed h-100 pos-r bgr-n bgpX-c bgpY-c bgsz-cv login-background">
                </div>
                <div className="col-12 col-md-4 peer pX-40 pY-20 h-100 bgc-white scrollable pos-r">
                    <h4 className="fw-300 c-grey-900 mB-20">Password Reset</h4>

                    <div className="form-group">
                        <label className="text-normal text-dark">Email</label>
                        <input type="email" className="form-control" ref="email" />
                    </div>

                    <div className="form-group">
                        <div className="peers ai-c jc-sb fxw-nw">
                            <div className="peer">
                                <button onClick={this.handleClick} className="btn btn-primary">Send Password Reset</button>
                                <Link to="/" role="button" className="btn btn-danger mL-10">Cancel</Link>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default Reset