import React from 'react'
import { Link } from 'react-router-dom'
import Logo from '../../assets/images/logo.png'

class Login extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            email: '',
            password: ''
        }
    }

    handleClick = () => {
        this.setState({
            email: this.refs.email.value,
            password: this.refs.password.value
        })
        this.props.history.push('/home')
    }

    render() {
        return (
            <div className="peers ai-s fxw-nw h-100vh">
                <div className="d-n@sm- peer peer-greed h-100 pos-r bgr-n bgpX-c bgpY-c bgsz-cv login-background">
                </div>
                <div className="col-12 col-md-4 peer pX-40 pY-80 h-100 bgc-white scrollable pos-r">
                    <div className="text-center">
                        <img className="login-logo" src={Logo} alt="Ripcurl Go" />
                    </div>
                    <h4 className="fw-300 c-grey-900 mB-40">Login</h4>
                    <div className="form-group">
                        <label className="text-normal text-dark">Email</label>
                        <input type="email" className="form-control" ref="email" />
                    </div>
                    <div className="form-group">
                        <label className="text-normal text-dark">Password</label>
                        <input type="password" className="form-control" ref="password" />
                    </div>
                    <div className="form-group">
                        <div className="peers ai-c jc-sb fxw-nw">
                            <div className="peer">
                                <div className="checkbox checkbox-circle checkbox-info peers ai-c">
                                    <input type="checkbox" id="inputCall1" name="inputCheckboxesCall" className="peer" />
                                    <label htmlFor="inputCall1" className=" peers peer-greed js-sb ai-c">
                                        <span className="peer peer-greed">Remember Me</span>
                                    </label>
                                </div>
                            </div>
                            <div className="peer ml-auto">
                                <Link className="peer peer-greed" to="/password/reset">Forgot password?</Link>
                            </div>
                        </div>
                    </div>
                    <div className="form-group">
                        <div className="peers ai-c jc-sb fxw-nw">
                            <div className="peer">
                                <button onClick={this.handleClick} className="btn btn-primary login-button">Login</button>
                            </div>
                        </div>
                    </div>
                    <div className="peer">
                        <span className="peer peer-greed">Don't Have An Account? <Link to="/register">Sign Up</Link></span>
                    </div>
                </div>
            </div>
        )
    }
}

export default Login