import React from 'react'
import { Link } from 'react-router-dom'

class ListOrder extends React.Component {

    currencyFormat(num) {
        return (
            'Rp.' + num.replace('.', ',').replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')
        )
    }

    render() {
        return (
            <div className="row">
                <div className="col-12 mb-3">
                    <div className="card">
                        <div className="card-body">
                            <div className="row">
                                <div className="col">
                                    <p className="font-weight-bold">
                                        {this.props.list.name}
                                    </p>
                                    {this.currencyFormat(this.props.list.price)}
                                </div>
                                <div className="col">
                                    <Link role="button" className="btn btn-success float-right text-white" to="/detail">Detail</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default ListOrder