import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import Header from '../partial/Header'
import SurfMap from '../partial/SurfMap'

class DetailOrder extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            course: {
                name: 'Canggu - Old\'s Man',
                price: "700000",
                product: 'Individual Beach Break Course 1-5',
                latitude: '-8.6593006',
                longitude: '115.1308194'
            },
            zoom: 17,
            marker: true

        }
    }

    currencyFormat(num) {
        return (
            'Rp.' + num.replace('.', ',').replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.')
        )
    }

    accept = () => {
        this.props.history.push('/instructor')
    }

    render() {
        return (
            <div className="container home mt-3">
                <Header />
                <div className="card">
                    <div className="card-body">
                        <div className="row mb-5">
                            <div className="col-12">
                                <p className="font-weight-bold">
                                    {this.state.course.name}
                                </p>
                                {this.currencyFormat(this.state.course.price)}<br />
                                {this.state.course.product}


                                <div className="form-group mt-3">
                                    <button class="btn btn-primary" type="button" onClick={this.accept}>Accept</button>
                                    <button class="btn btn-danger ml-2" type="button" onClick={this.accept}>Decline</button>
                                </div>
                            </div>

                        </div>

                        <SurfMap latitude={this.state.course.latitude} longitude={this.state.course.longitude} zoom={this.state.zoom} marker={this.state.marker} />

                    </div>
                </div>
            </div>
        )
    }
}

export default withRouter(DetailOrder)