import React from 'react'
import { Link } from 'react-router-dom'

class Menu extends React.Component {
    constructor(props) {
        super(props)
        this.state = {}
    }

    render() {
        return (
            <div className="col-6 col-sm-4 col-md-3 text-center mb-3">
                <Link to={this.props.link} className="menu-link">
                    <img src={this.props.image} alt={this.props.label} className="img-thumbnail" />
                    <br />
                    <label>{this.props.label}</label>
                </Link>
            </div>
        )
    }
}

export default Menu