import React from 'react'
import { withRouter, Link } from 'react-router-dom'
import Logo from '../../assets/images/logo.png'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

class Header extends React.Component {

    logout = () => {
        this.props.history.push('/')
    }

    render() {
        return (
            <nav class="navbar navbar-expand-lg navbar-dark bg-dark">
                <Link class="navbar-brand" to="/home"><img src={Logo} alt="Ripcurl GO" className="home-logo" /></Link>
                <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                    <span class="navbar-toggler-icon"></span>
                </button>
                <div class="collapse navbar-collapse" id="navbarNav">
                    <ul class="navbar-nav ml-auto">
                        <li class="nav-item active">
                            <Link class="nav-link" to="/home" title="Home"> <FontAwesomeIcon icon="home" className="fa-lg" /> <span class="sr-only">(current)</span></Link>
                        </li>
                        <li class="nav-item">
                            <Link class="nav-link" to="/info" title="Info"><FontAwesomeIcon icon="info-circle" className="fa-lg" /></Link>
                        </li>
                        <li class="nav-item dropdown">
                            <Link title="User" class="nav-link dropdown-toggle" to="#" id="navbarDropdownMenuLink" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                <FontAwesomeIcon icon="user-circle" className="fa-lg" />
                            </Link>
                            <div class="dropdown-menu" aria-labelledby="navbarDropdownMenuLink">
                                <Link class="dropdown-item" to="/profile">Profile</Link>
                                <Link class="dropdown-item" to="/schedule">Schedule</Link>
                                <a class="dropdown-item" role="button" onClick={this.logout}>Logout</a>
                            </div>
                        </li>
                    </ul>
                </div>
            </nav>
        )
    }
}

export default withRouter(Header)

