import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom';
import Login from './page/auth/Login'
import Register from './page/auth/Register'
import Reset from './page/auth/Reset'
import Dashboard from './page/admin/Dashboard'
import Home from './page/home/Home'
import Adults from './page/course/Adults'
import Couple from './page/course/Couple'
import Family from './page/course/Family'
import Kids from './page/course/Kids'
import Private from './page/course/Private'
import Rentals from './page/course/Rentals'
import Purchase from './page/partial/Purchase'
import Info from './page/home/Info'
import Instructor from './page/instructor/Order'
import Detail from './page/instructor/DetailOrder'

import { library } from '@fortawesome/fontawesome-svg-core'
import { fab } from '@fortawesome/free-brands-svg-icons'
import { faChevronLeft, faInfoCircle, faHome, faUserCircle } from '@fortawesome/free-solid-svg-icons'


function App() {
  library.add(faChevronLeft, fab, faInfoCircle, faHome, faUserCircle)

  return (
    <Router>
      <Route name="login" exact path="/" component={Login} />
      <Route name="register" exact path="/register" component={Register} />
      <Route name="reset" exact path="/password/reset" component={Reset} />
      <Route name="reset" exact path="/dashboard" component={Dashboard} />
      <Route name="home" exact path="/home" component={Home} />
      <Route name="adults" exact path="/adults" component={Adults} />
      <Route name="couple" exact path="/couple" component={Couple} />
      <Route name="family" exact path="/family" component={Family} />
      <Route name="kids" exact path="/kids" component={Kids} />
      <Route name="private" exact path="/private" component={Private} />
      <Route name="rentals" exact path="/rentals" component={Rentals} />
      <Route name="purchase" exact path="/purchase" component={Purchase} />
      <Route name="info" exact path="/info" component={Info} />
      <Route name="instructor" exact path="/instructor" component={Instructor} />
      <Route name="detail" exact path="/detail" component={Detail} />
    </Router>
  );
}

export default App;
