import React from 'react'
import { Link } from 'react-router-dom'

class Register extends React.Component {

    constructor(props) {
        super(props)
        this.state = {
            username: '',
            password: ''
        }
    }

    handleClick = () => {
        this.setState({
            username: this.refs.username.value,
            password: this.refs.password.value
        })
        this.props.history.push('/home')
    }

    render() {
        return (
            <div className="peers ai-s fxw-nw h-100vh">
                <div className="d-n@sm- peer peer-greed h-100 pos-r bgr-n bgpX-c bgpY-c bgsz-cv login-background">
                </div>
                <div className="col-12 col-md-4 peer pX-40 pY-10 h-100 bgc-white scrollable pos-r">
                    <h4 className="fw-300 c-grey-900 mB-10">Register</h4>

                    <ul class="nav nav-pills mb-3" id="pills-tab" role="tablist">
                        <li class="nav-item">
                            <a class="nav-link active" id="pills-home-tab" data-toggle="pill" href="#pills-home" role="tab" aria-controls="pills-home" aria-selected="true">User</a>
                        </li>
                        <li class="nav-item">
                            <a class="nav-link" id="pills-profile-tab" data-toggle="pill" href="#pills-profile" role="tab" aria-controls="pills-profile" aria-selected="false">Instructor</a>
                        </li>
                    </ul>
                    <div class="tab-content" id="pills-tabContent">
                        <div class="tab-pane fade show active" id="pills-home" role="tabpanel" aria-labelledby="pills-home-tab">
                            <div className="form-group">
                                <label className="text-normal text-dark">Name</label>
                                <input type="text" className="form-control" ref="name" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Email</label>
                                <input type="email" className="form-control" ref="username" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Password</label>
                                <input type="password" className="form-control" ref="password" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Password Confirmation</label>
                                <input type="password" className="form-control" ref="password-confirmation" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Phone</label>
                                <input type="text" className="form-control" ref="phone" />
                            </div>
                            <div className="form-group">
                                <div className="peers ai-c jc-sb fxw-nw">
                                    <div className="peer">
                                        <button onClick={this.handleClick} className="btn btn-primary login-button">Register</button>
                                        <Link to="/" role="button" className="btn btn-danger cancel-button mL-10">Cancel</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div class="tab-pane fade" id="pills-profile" role="tabpanel" aria-labelledby="pills-profile-tab">
                            <div className="form-group">
                                <label className="text-normal text-dark">Name</label>
                                <input type="text" className="form-control" ref="name" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Email</label>
                                <input type="email" className="form-control" ref="email" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Password</label>
                                <input type="password" className="form-control" ref="password" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Password Confirmation</label>
                                <input type="password" className="form-control" ref="password-confirmation" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Phone</label>
                                <input type="text" className="form-control" ref="phone" />
                            </div>
                            <div className="form-group">
                                <label className="text-normal text-dark">Address</label>
                                <textarea row="2" className="form-control" ref="address"></textarea>
                            </div>
                            <div className="form-group">
                                <div className="peers ai-c jc-sb fxw-nw">
                                    <div className="peer">
                                        <button onClick={this.handleClick} className="btn btn-primary login-button">Register</button>
                                        <Link to="/" role="button" className="btn btn-danger cancel-button mL-10">Cancel</Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>
            </div>
        )
    }
}

export default Register