import React from 'react'
import { Link } from 'react-router-dom'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import SurfMap from './SurfMap'


class Purchase extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            level: ['Beginner', 'Intermediate', 'Advanced'],
            area: ['Canggu', 'Uluwatu', 'Kedungu', 'Kuta', 'Seminyak', 'Legian', 'Serangan', 'Sanur', 'Nusa Dua'],
            location: [
                { name: 'Canggu - Old\'s Man', latitude: '-8.6593006', longitude: '115.1308194' },
                { name: 'Canggu - Berawa Beach', latitude: '-8.6638801', longitude: '115.1359524' },
                { name: 'Canggu - Echo Beach', latitude: '-8.655188', longitude: '115.1254255' },
                { name: 'Seminyak - Petinget', latitude: '-8.6823303', longitude: '115.1508151' },
                { name: 'Seminyak - Ku De Ta', latitude: '-8.6871397', longitude: '115.1543933' },
                { name: 'Legian - Double Six', latitude: '-8.696179', longitude: '115.1607016' },
                { name: 'Legian - Ripcurl School Of Surf', latitude: '-8.6990196', longitude: '115.1623692' },
                { name: 'Kuta - Padma', latitude: '-8.7068428', longitude: '115.1655942' },
                { name: 'Kuta - Kuta Reef', latitude: '-8.741088', longitude: '115.1605993' },
                { name: 'Kuta - Airports', latitude: '-8.7436159', longitude: '115.1623222' },
                { name: 'Kedungu', latitude: '-8.6088824', longitude: '115.083265' },
                { name: 'Klotok', latitude: '-8.5771925', longitude: '115.4013873' },
                { name: 'Serangan', latitude: '-8.7288674', longitude: '115.2245785' },
                { name: 'Uluwatu - Balangan', latitude: '-8.7917115', longitude: '115.1231536' },
                { name: 'Uluwatu - Dreamland', latitude: '-8.7992214', longitude: '115.1177415' },
                { name: 'Uluwatu - Bingin', latitude: '-8.805642', longitude: '115.1130604' },
                { name: 'Uluwatu - Impossibles', latitude: '-8.8074581', longitude: '115.1088268' },
                { name: 'Uluwatu - Baby Padang', latitude: '-8.8109885', longitude: '115.1033904' },
                { name: 'Uluwatu - Padang Padang', latitude: '-8.8109885', longitude: '115.1033904' },
                { name: 'Uluwatu - uluwatu', latitude: '-8.8149189', longitude: '115.0883183' },
                { name: 'Sanur', latitude: '-8.6735785', longitude: '115.2633788' }
            ],
            latitude: '',
            longitude: '',
            zoom: 0,
            marker: false
        }
    }

    back = () => {
        this.props.history.goBack()
    }

    surfArea = (e) => {
        let location = this.state.location[e.target.value]
        this.setState({
            latitude: location.latitude,
            longitude: location.longitude,
            zoom: 17,
            marker: true
        })
    }

    render() {
        return (
            <div className="container menu mt-3">
                <div className="card">
                    <div className="card-header menu-header">
                        <button className="back-button" onClick={this.back}><FontAwesomeIcon icon="chevron-left" /></button>
                        <div className="text-center">Choose Location</div>
                    </div>
                    <div className="card-body">

                        <div className="masonry-item col-md-12">
                            <div className="bgc-white p-20 bd">
                                <h6 className="c-grey-900">Find Perfect Place</h6>
                                <div className="mT-30">
                                    <form>

                                        <div className="checkbox checkbox-circle checkbox-info peers ai-c mB-15">
                                            <input type="checkbox" id="inputCall1" name="inputCheckboxesCall" className="peer" />
                                            <label htmlFor="inputCall1" className=" peers peer-greed js-sb ai-c">
                                                <span className="peer peer-greed">Bring your own board</span>
                                            </label>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="inputEmail3">Surf Level</label>
                                            <select id="inputState" className="form-control">
                                                {
                                                    this.state.level.map(value => <option>{value}</option>)
                                                }
                                            </select>
                                        </div>
                                        <div className="form-group">
                                            <label htmlFor="area">Surf Area</label>
                                            <select name="area" className="form-control" onSelect={this.surfArea}>
                                                {
                                                    this.state.location.map((value, index) => <option value={index} selected={index === 6}> {value.name}</option>)
                                                }
                                            </select>
                                        </div>

                                        <div className="form-group">
                                            <button class="mt-c-btn-buy-now btn btn-primary" type="button" data-mt-source="https://megatix.co.id/white-label/surf">
                                                Purchase
                                            </button>
                                        </div>
                                    </form>
                                </div>


                                <SurfMap latitude={this.state.latitude} longitude={this.state.longitude} zoom={this.state.zoom} marker={this.state.marker} />

                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Purchase